* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

body {
  display: -ms-grid;
  display: grid;
  place-items: center;
  background-color: #fafbfb;
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-width: 550px;
  min-height: 500px;
  background-color: #282d35;
}

.navbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 35px 27px;
  background-color: #21222a;
}

.navbar div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.navbar div img {
  height: 28px;
  margin-right: 5px;
}

.navbar div p {
  color: #61dafb;
  font-weight: 700;
  font-size: 23px;
  line-height: 27px;
  letter-spacing: 1px;
}

.navbar p {
  font-weight: 600;
  font-size: 16px;
  color: #deebf8;
}

.main {
  position: relative;
  color: #fff;
}

.main img {
  position: absolute;
  top: 110px;
  right: 0;
}

.main p {
  font-weight: 700;
  font-size: 39px;
  margin-bottom: 46px;
  margin: 57px 0 0 30px;
}

.main ul {
  margin: 30px 0 0 65px;
}

.main ul li::marker {
  font-size: 20px;
  color: #61dafb;
}

.main ul li {
  font-weight: 400;
  font-size: 16px;
  margin-bottom: 20px;
}

@media only screen and (max-width: 400px) {
  .container {
    min-width: 280px;
    min-height: 280px;
    max-width: 280px;
    max-height: 280px;
  }
  .navbar {
    padding: 20px 17px;
  }
  .navbar img {
    height: 18px;
    margin-right: 5px;
  }
  .navbar div {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  .navbar div img {
    height: 18px;
    margin-right: 3px;
  }
  .navbar div p {
    font-size: 12px;
  }
  .navbar p {
    font-size: 10px;
  }
  .main img {
    top: 50px;
    width: 100px;
  }
  .main p {
    z-index: 3;
    font-size: 15px;
    margin-bottom: 36px;
    margin: 20px 0 0 20px;
  }
  .main ul {
    z-index: 3;
    margin: 10px 0 0 40px;
  }
  .main ul li::marker {
    font-size: 12px;
  }
  .main ul li {
    z-index: 3;
    font-size: 9px;
    margin-bottom: 10px;
  }
}
